.card-container{-webkit-backface-visibility:hidden;backface-visibility:hidden;background:#90cece;border:1px solid #000;border-radius:5px;cursor:pointer;display:flex;flex-direction:column;padding:5px 10px;transition:transform .3s ease}.card-container img{width:70%;margin:auto}.card-container:hover{transform:scale(1.05)}.card-container h2,.card-container p{text-align:center}

.card-list{display:grid;grid-gap:15px;gap:15px;grid-template-columns:1fr;margin:0 auto;width:85vw}@media (min-width: 768px){.card-list{grid-template-columns:1fr 1fr 1fr 1fr}}

.App {
  margin-bottom: 20px;
}

.App input {
  display: block;
  font-size: 20px;
  margin: 20px auto;
  padding: 5px;
}

h1 {
  color: #90cece;
  font-family: 'Cormorant Unicase', serif;
  font-size: 50px;
  margin-bottom: 0;
  text-align: center;
}
